import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import IconButton from '@material-ui/core/IconButton';
import StarBorderIcon from '@material-ui/icons/StarBorder';
import axios from "axios";
import "react-image-gallery/styles/css/image-gallery.css";

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
  },
  gridList: {
    width: 500,
    height: 450,
    // Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS.
    transform: 'translateZ(0)',
  },
  titleBar: {
    background:
      'linear-gradient(to bottom, rgba(0,0,0,0.7) 0%, ' +
      'rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)',
  },
  icon: {
    color: 'white',
  },
}));

/**
 * The example data is structured as follows:
 *
 * import image from 'path/to/image.jpg';
 * [etc...]
 *
 * const tileData = [
 *   {
 *     img: image,
 *     title: 'Image',
 *     author: 'author',
 *     featured: true,
 *   },
 *   {
 *     [etc...]
 *   },
 * ];
 */
export default function AdvancedGridList() {
  const classes = useStyles();
  const [images, setImages] = React.useState(null);
  const [selectedImage, setSelectedImage] = React.useState(null);

  React.useEffect(() => {
    let shouldCancel = false;
 
    const call = async () => {
      const response = await axios.get(
        "https://google-photos-album-demo.glitch.me/dChGhhSBLg8RhSg27"
      );
      if (!shouldCancel && response.data && response.data.length > 0) {
        setImages(
          response.data.map(url => ({
            original: `${url}=w1024`,
            thumbnail: `${url}=w100`,
            url: url,
            featured: Math.floor(Math.random() * (2 - 1 + 1) + 1),
          }))
        );
      }
    };
    call();
    return () => (shouldCancel = true);
}, []);

  return (
    <div className={classes.root}>
        {
      images!=null && (
      <GridList cellHeight={200} spacing={1} className={classes.gridList}>
        {images.map((tile) => (
          <GridListTile key={tile.url} cols={tile.featured ? 2 : 1} rows={tile.featured ? 2 : 1}>
            <img src={tile.original} alt={"A"} />
            <GridListTileBar
              title={"A"}
              titlePosition="top"
              actionIcon={
                <IconButton aria-label={`star ${tile.url}`} className={classes.icon}
                >
                  <StarBorderIcon  value={tile.url} id={tile.url} key={tile.url}
                 onClick={()=>{ console.log(tile.featured)}} />
                </IconButton>
              }
              actionPosition="left"
              className={classes.titleBar}
            />
          </GridListTile>
        ))}
      </GridList>
      )}
    </div>
  );
}